<template>
	<div class="sub-container join">
		<h2 class="txt-center marB-50">회원가입</h2>
		<div class="input-box">
			<div class="user-input disabled">
				<input
					type="text"
					ref="userId"
					v-model="id"
					placeholder="아이디"
					autocomplete="off"
					disabled
				/>
			</div>
			<div class="user-input">
				<input
					ref="userName"
					type="text"
					v-model="user_name"
					v-on:blur="checkName"
					placeholder="이름"
					autocomplete="off"
				/>
				<div class="err-msg-container">
					<Exclamation v-if="check_name_msg"></Exclamation>
					<span
						id="check_name"
						v-bind:class="{ redFont: !isValidName, msg: true }"
						>{{ check_name_msg }}</span
					>
				</div>
			</div>
			<div class="user-input">
				<input
					type="password"
					ref="userPwd"
					v-model="pwd"
					v-on:blur="checkPassword"
					placeholder="기존 비밀번호"
				/>
				<div class="err-msg-container">
					<Exclamation v-if="check_password_msg"></Exclamation>
					<span
						class="msg"
						id="check_pwd"
						v-bind:class="{ redFont: !isValidPassword, msg: true }"
						>{{ check_password_msg }}</span
					>
				</div>
			</div>
			<div class="user-input">
				<input
					type="password"
					ref="userPwd"
					v-model="newPwd"
					v-on:blur="checkNewPassword"
					placeholder="새로운 비밀번호"
				/>
				<div class="err-msg-container">
					<Exclamation v-if="check_new_password_msg"></Exclamation>
					<span
						class="msg"
						id="check_pwd"
						v-bind:class="{ redFont: !isValidPassword, msg: true }"
						>{{ check_password_msg }}</span
					>
				</div>
			</div>
			<div class="user-input">
				<input
					ref="pwdConfirm"
					type="password"
					v-model="newPwdConfirm"
					v-on:blur="checkNewPasswordConfirm"
					placeholder="새로운 비밀번호 재입력"
				/>
				<div id="cf_pwd"></div>
				<div class="err-msg-container">
					<Exclamation v-if="check_new_password_confirm_msg"></Exclamation>
					<span
						class="msg"
						id="confirm_pwd"
						v-bind:class="{ redFont: !isValidPasswordConfirm, msg: true }"
						>{{ check_new_password_confirm_msg }}</span
					>
				</div>
			</div>
			<div class="user-input disabled">
				<input
					ref="userEmail"
					type="text"
					v-model="user_email"
					placeholder="이메일"
					autocomplete="off"
					disabled
				/>
			</div>
			<div class="user-input">
				<input
					ref="userPhone"
					type="text"
					v-model="user_phone"
					placeholder="전화번호"
					v-on:blur="checkPhone"
					autocomplete="off"
				/>
				<div class="err-msg-container">
					<Exclamation v-if="check_phone_msg"></Exclamation>
					<span
						class="msg"
						id="check_phone"
						v-bind:class="{ redFont: !isValidPhone, msg: true }"
						>{{ check_phone_msg }}</span
					>
				</div>
			</div>
			<div class="user-input zipcode">
				<input
					ref="zipcode"
					type="text"
					placeholder="우편번호"
					v-model="getZipcode"
					@change="checkstreet1"
					@input="checkstreet1"
					autocomplete="off"
					readonly
				/>
				<button
					class="btn dark full"
					type="button"
					@click="execDaumPostcode"
					:checkstreet="checkstreet1"
				>
					검색
				</button>
			</div>
			<div class="user-input">
				<input
					ref="street1"
					type="text"
					placeholder="주소"
					v-model="getstreet1"
					autocomplete="off"
					readonly
				/>
				<div class="err-msg-container">
					<Exclamation v-if="street1_msg"></Exclamation>
					<span
						class="msg"
						id="check_phone"
						v-bind:class="{ redFont: street1_msg, msg: true }"
					>
						{{ street1_msg }}
					</span>
				</div>
			</div>
			<div class="user-input">
				<input
					ref="street2"
					type="text"
					v-model="street2"
					placeholder="상세주소"
					v-on:blur="checkstreet2"
					autocomplete="off"
				/>
				<div class="err-msg-container">
					<Exclamation v-if="street2_msg"></Exclamation>
					<span
						class="msg"
						id="check_phone"
						v-bind:class="{ redFont: !isValidstreet2, msg: true }"
					>
						{{ street2_msg }}
					</span>
				</div>
			</div>
			<div class="user-input">
				<input
					type="checkbox"
					class="checkbox"
					name="member"
					id="approve_newsletter"
					v-model="approve_newsletter"
				/>
				<label class="marR-5" for="approve_newsletter"></label>
				<span>(선택)뉴스레터 및 광고를 수신하시겠습니까?</span>
			</div>
			<div class="user-input">
				<button
					class="btn full dark"
					id="join_submit"
					type="button"
					@click="updateUser"
				>
					확인
				</button>
			</div>
		</div>

		<div class="find-box">
			<p @click="signOut()">회원탈퇴</p>
		</div>

		<NotificationModal
			v-if="isModalShown"
			:msg="modalMsg"
			@changeIsShown="changeIsShown"
		></NotificationModal>
		<ConfirmModal
			v-if="deleteModal"
			:msg="`탈퇴하시겠습니까? 탈퇴 후 해당 계정은 복구되지 않습니다`"
			:single="false"
			@confirm="signOut"
			@cancel="cancel"
		>
		</ConfirmModal>
	</div>
</template>

<script>
import {
	validatePassword,
	validatePhone,
	validateName,
} from '@/utils/validation';
import { mapGetters } from 'vuex';
import {
	fetchUserDetail,
	signOut,
	updateUser,
	checkPasswordAgain,
} from '@/api/user';
import Store from '@/store';
import Exclamation from '@/assets/icon/Exclamation';
import NotificationModal from '@/components/common/NotificationModal';
import ConfirmModal from '@/components/common/ConfirmModal.vue';

export default {
	name: 'Login',
	components: { Exclamation, NotificationModal, ConfirmModal },
	computed: {
		...mapGetters(['getZipcode', 'getstreet1', 'getIdentity', 'getId']),
	},
	created() {
		let address = {};
		let id = this.$route.params.id;
		this.fetchUserDetail(id);
		Store.commit('SET_ADDRESS', address);
	},
	data() {
		return {
			id: '',
			pwd: '',
			newPwd: '',
			newPwdConfirm: '',
			user_name: '',
			user_phone: '',
			user_email: '',
			isValidPassword: false,
			check_password_msg: '',
			isValidNewPassword: false,
			check_new_password_msg: '',
			isValidPasswordConfirm: false,
			check_new_password_confirm_msg: '',
			isValidName: false,
			check_name_msg: '',
			isValidPhone: false,
			check_phone_msg: '',
			street1_msg: '',
			isValidstreet1: false,
			street2: '',
			street2_msg: '',
			isValidstreet2: false,
			approve_newsletter: false,
			deleteModal: false,
			updateModal: false,
			isModalShown: false,
			modalMsg: '',
			authNumber: '',
		};
	},

	methods: {
		execDaumPostcode() {
			let address = {};
			window.daum.postcode.load(function() {
				new window.daum.Postcode({
					oncomplete: data => {
						// 팝업에서 검색결과 항목을 클릭했을때 실행할 코드를 작성하는 부분.

						// 도로명 주소의 노출 규칙에 따라 주소를 표시한다.
						// 내려오는 변수가 값이 없는 경우엔 공백('')값을 가지므로, 이를 참고하여 분기 한다.
						var roadAddr = data.roadAddress; // 도로명 주소 변수
						address.roadAddress = roadAddr;
						var zipcode = data.zonecode;
						address.zipcode = zipcode;

						// 우편번호와 주소 정보를 해당 필드에 넣는다.
						//document.getElementById('sample4_postcode').value = data.zonecode;
						address.numberAddress = data.jibunAddress;

						Store.commit('SET_ADDRESS', address);
					},
				}).open();
			});
		},
		async fetchUserDetail(id) {
			let { data } = await fetchUserDetail(id);
			console.log(data);

			let address = {
				zipcode: data.zipcode,
				roadAddress: data.street1,
			};

			Store.commit('SET_ADDRESS', address);

			console.log(this.getZipcode);

			this.id = data.identity;
			this.user_name = data.name;
			this.user_phone = data.phone;
			this.user_email = data.email;
			this.approve_newsletter = data.agreeYn == 'Y' ? true : false;
		},
		checkName() {
			const value = this.user_name;
			if (!value) {
				this.check_name_msg = '이름을 입력해주세요.';
				this.isValidName = false;
			} else if (!validateName(value)) {
				this.check_name_msg = '8자리로 이하로 입력해주세요.';
				this.isValidName = false;
			} else {
				this.check_name_msg = '';
				this.isValidName = true;
			}
		},
		checkPassword() {
			const value = this.pwd;
			if (!value) {
				this.check_password_msg = '기존 비밀번호를 입력해주세요.';
				this.isValidPassword = false;
			} else if (!validatePassword(value)) {
				this.check_password_msg =
					'영문, 숫자,특수문자 필수조합 20자 이하로 입력';
				this.isValidPassword = false;
			} else {
				this.check_password_msg = '';
				this.isValidPassword = true;
			}
		},
		checkNewPassword() {
			const value = this.newPwd;
			if (!value) {
				this.check_pacheck_new_password_msgssword_msg =
					'새 비밀번호를 입력해주세요.';
				this.isValidNewPassword = false;
			} else if (!validatePassword(value)) {
				this.check_new_password_msg =
					'영문, 숫자,특수문자 필수조합 20자 이하로 입력';
				this.isValidNewPassword = false;
			} else {
				this.check_new_password_msg = '';
				this.isValidNewPassword = true;
			}
		},
		checkNewPasswordConfirm() {
			const value = this.newPwdConfirm;
			if (!value) {
				this.check_new_password_confirm_msg =
					'비밀번호 확인을 위해 한번 더 입력해주세요';
				this.isValidPasswordConfirm = false;
			} else if (!this.isPasswordSame()) {
				this.check_new_password_confirm_msg = '비밀번호가 일치하지 않습니다.';
				this.isValidPasswordConfirm = false;
			} else {
				this.check_new_password_confirm_msg = '';
				this.isValidPasswordConfirm = true;
			}
		},
		checkPhone() {
			const value = this.user_phone;
			if (!value) {
				this.check_phone_msg = '휴대폰 번호를 입력해주세요.';
				this.isValidPhone = false;
			} else if (!validatePhone(value)) {
				this.check_phone_msg = '연락처 양식에 맞게 작성해주세요';
				this.isValidPhone = false;
			} else {
				this.check_phone_msg = '';
				this.isValidPhone = true;
			}
		},
		isPasswordSame() {
			if (this.newPwd === this.newPwdConfirm) {
				return true;
			} else {
				return false;
			}
		},
		checkstreet1() {
			const value = this.getstreet;
			if (!value) {
				this.street1_msg = '주소를 입력해주세요';
				this.isValidstreet1 = false;
			} else {
				this.street1_msg = '';
				this.isValidstreet1 = true;
			}
		},
		checkstreet2() {
			const value = this.street2;
			if (!value) {
				this.street2_msg = '상세주소를 입력해주세요';
				this.isValidstreet2 = false;
			} else {
				this.street2_msg = '';
				this.isValidstreet2 = true;
			}
		},
		async updateUser() {
			console.log('clicked');
			if (!this.isValidName) {
				this.checkName();
				return false;
			}

			if (!this.isValidPassword) {
				this.checkPassword();
				return false;
			}

			if (!this.isValidPasswordConfirm) {
				this.checkPasswordConfirm();
				return false;
			}

			if (!this.isValidPhone) {
				this.checkPhone();
				return false;
			}

			if (!this.getstreet1) {
				this.modalMsg = '주소를 입력해주세요';
				console.log(this.isModalShown);
				this.changeIsShown(true);
				console.log(this.isModalShown);
				return false;
			}

			if (!this.isValidstreet2) {
				this.checkstreet2();
				return false;
			}

			if (this.pwd) {
				let pwd = {
					identity: this.getIdentity,
					password: this.pwd,
				};

				let result = await checkPasswordAgain(pwd);
				if (result.data.successYn == 'N') {
					this.updateModal = false;
					this.modalMsg = '기존 비밀번호가 일치하지 않습니다.';
					this.changeIsShown(true);

					return false;
				}
			}

			let userData = {
				identity: this.id,
				email: this.user_email,
				password: this.newPwd,
				name: this.user_name,
				phone: this.user_phone,
				zipcode: this.getZipcode,
				street1: this.getstreet1,
				street2: this.street2,
				agreeYn: this.approve_newsletter ? 'Y' : 'N',
			};

			let result;
			try {
				result = await updateUser(this.getId, userData);
				if (result.status == 200) {
					this.updateModal = false;
					this.modalMsg = '회원정보가 수정되었습니다.';
					this.changeIsShown(true);
					this.$router.go();
				} else {
					this.updateModal = false;
					this.modalMsg = '정보수정에 실패하였습니다';
					this.changeIsShown(true);
				}
			} catch (e) {
				this.updateModal = false;
				this.modalMsg = '정보수정에 실패하였습니다!';
				this.changeIsShown(true);
			}
		},
		async signOut() {
			let result;
			try {
				result = await signOut();
				if (result.data.status == 200) {
					alert('탈퇴성공!');
				} else {
					alert('탈퇴실패!');
				}
			} catch (e) {
				console.log(e);
			}
		},
		changeIsShown(isShown) {
			this.isModalShown = isShown;
		},
		cancel() {
			this.updateModal = false;
			this.deleteModal = false;
		},
	},
};
</script>

<style lang="scss" scoped>
.user-input.disabled {
	background: #00ad84;
	color: #231815;
}

.user-input.zipcode {
	display: flex;
	padding: 0;
	.btn {
		box-sizing: border-box;
		height: 55px;
		width: 50%;
	}
}

.auth-info-box {
	margin: 50px auto 30px;
}
</style>
